.croppie-container {
  padding: 30px;
}

.croppie-container .cr-image {
  z-index: -1;
  transform-origin: 0 0;
  max-width: none;
  position: absolute;
  top: 0;
  left: 0;
}

.croppie-container .cr-boundary {
  z-index: 1;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.croppie-container .cr-viewport {
  z-index: 0;
  border: 2px solid #fff;
  margin: auto;
  position: absolute;
  inset: 0;
  box-shadow: 0 0 2000px 2000px #00000080;
}

.croppie-container .cr-vp-circle {
  border-radius: 50%;
}

.croppie-container .cr-overlay {
  z-index: 1;
  cursor: move;
  position: absolute;
}

.croppie-container .cr-slider-wrap {
  width: 75%;
  text-align: center;
  margin: 25px auto 0;
}

.croppie-result {
  position: relative;
  overflow: hidden;
}

.croppie-result img {
  position: absolute;
}

.croppie-container .cr-image, .croppie-container .cr-overlay, .croppie-container .cr-viewport {
  transform: translateZ(0);
}

.cr-slider {
  -webkit-appearance: none;
  width: 300px;
  max-width: 100%;
}

.cr-slider::-webkit-slider-runnable-track {
  width: 100%;
  height: 3px;
  background: #00000080;
  border: 0;
  border-radius: 3px;
}

.cr-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 16px;
  width: 16px;
  background: #ddd;
  border: none;
  border-radius: 50%;
  margin-top: -6px;
}

.cr-slider:focus {
  outline: none;
}

.cr-slider::-moz-range-track {
  width: 100%;
  height: 3px;
  background: #00000080;
  border: 0;
  border-radius: 3px;
}

.cr-slider::-moz-range-thumb {
  height: 16px;
  width: 16px;
  background: #ddd;
  border: none;
  border-radius: 50%;
  margin-top: -6px;
}

.cr-slider:-moz-focusring {
  outline-offset: -1px;
  outline: 1px solid #fff;
}

.cr-slider::-ms-track {
  width: 300px;
  height: 5px;
  color: #0000;
  background: none;
  border-width: 6px 0;
  border-color: #0000;
}

.cr-slider::-ms-fill-lower {
  background: #00000080;
  border-radius: 10px;
}

.cr-slider::-ms-fill-upper {
  background: #00000080;
  border-radius: 10px;
}

.cr-slider::-ms-thumb {
  height: 16px;
  width: 16px;
  background: #ddd;
  border: none;
  border-radius: 50%;
}

.cr-slider:focus::-ms-fill-lower {
  background: #00000080;
}

.cr-slider:focus::-ms-fill-upper {
  background: #00000080;
}

.cr-rotate-controls {
  z-index: 1;
  position: absolute;
  bottom: 5px;
  left: 5px;
}

.cr-rotate-controls button {
  background: none;
  border: 0;
}

.cr-rotate-controls i:before {
  font-size: 22px;
  font-style: normal;
  font-weight: 900;
  display: inline-block;
}

.cr-rotate-l i:before {
  content: "↺";
}

.cr-rotate-r i:before {
  content: "↻";
}

